.about {
    background-color:rgba(0,0,0,.5);;
    margin: 100px auto;
    border-radius: 10px;
    padding: 20px;
}

.about__content
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    padding: 1rem;
    text-align: center;
}

.about__content h2 {
    padding-top: 20px;
}

.about__content p {
justify-content: center;
display: flex;
}

.about__me {
    width: 50px;
    height: 50px;
    border-radius: 2rem;
    display: grid;
    place-items: center;
    padding: 1rem;
    padding-top: 10px;
    padding-bottom: 10px;
    margin: 1rem;
    border-radius: 50px;
  }

.about__me-image img {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    padding-bottom: 30px;
  }

