

.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
    margin: auto;
    
  }

  .contact {
    position: relative;
    margin: auto;
    background-color:rgba(0,0,0,.5);
    border-radius: 10px;
    margin-bottom: 20px;
    margin-top: 20px;
    padding: 20px;
    padding-bottom: 40px;
  }

  .contact h2 {
    text-align: center;
    margin: 1rem;
    color: white;
  }
  
  .contact__options {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
  }
  
  .contact__option {
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
  }
  
  .contact__option:hover {
    background: transparent;
    border-color: green;
  }
  
  .contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }
  
  .contact__option h5 {
    color: yellowgreen;
  }
  
  .contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
  }
  
  /* ================= Form================== */
  form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.2rem;
    background: transparent;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: white;
    border: 2px solid yellowgreen;
    resize: none;
    
  }

  .nameInput {
   grid-column: 1/2;
   grid-row: 1
  }

    .emailInput {
    grid-column: 2/3;
    grid-row: 1 
    }

    .textInput {
        grid-column: 1/3;
        grid-row: 2/3;
      }

    .btn.btn.btn-primary {
        grid-column: 1/3;
        grid-row: 3/4;
      }
  
  /* ================ Medium Device Media Queries ================= */
  @media screen and (max-width: 1024px) {
    .container.contact__container {
      grid-template-columns: 1fr;
      gap: 2rem;
    }
  }
  
  /* ================ Small Device Media Queries ================= */
  @media screen and (max-width: 600px) {
    .container.contact__container {
      width: var(--container-width-sm);
    }
  }
  
